    border: none;
    border-radius: 0;
    border-bottom: 5px solid #50d19e; }
    .input-field .form-input:focus {
      outline: none; }
    .input-field .form-input:focus ~ .form-input-label {
      top: -1.45em;
      left: 0.1em;
      font-size: 12px;
      font-weight: bold;
      color: white;
      letter-spacing: 0; }
    .input-field .form-input:required {
      box-shadow: none; }
