  .input-field input[type="password"] {
    letter-spacing: 0.1em; }
  .input-field .form-input-label {
    text-transform: uppercase;
    color: #2e2e31;
    font-size: 0.9em;
    font-weight: normal;
    letter-spacing: 0.05em;
    position: absolute;
    pointer-events: none;
    left: 0.5em;
    top: 0.4em;
    -webkit-transition: 300ms ease all;
    transition: 300ms ease all; }
    .input-field .form-input-label.shrink {
      top: -1.45em;
      left: 0.1em;
      font-size: 12px;
      font-weight: bold;
      color: white;
      letter-spacing: 0; }

